import React, { Component } from "react";
import "../App.css";

class Item extends Component {
  render() {
    const { item, handleCheckItem } = this.props;
    const classIcon = item.lock
      ? "fas fa-ban hidden"
      : item.selected
      ? "far fa-check-square"
      : "far fa-square";

    return (
      <div className="item">
        <span>{item.name}</span>

        <div className="btnCheckbox" onClick={() => handleCheckItem()}>
          <i className={classIcon} />
        </div>
      </div>
    );
  }
}

export default class ItemList extends Component {
  state = { showList: true };

  toggleList = () => {
    this.setState({ showList: !this.state.showList });
  };

  render() {
    const { list, handleCheckAllItem, handleCheckItem } = this.props;

    const classCheckAll = list.lock
      ? "far fa-minus-square hidden"
      : list.selected
      ? "far fa-minus-square"
      : "far fa-square";

    const listItems = list.itemList.map((item, key) => (
      <Item
        key={key}
        item={item}
        handleCheckItem={() => handleCheckItem(list.id, item.id)}
      />
    ));

    const classList = this.state.showList ? "itemList" : "itemList hide";

    return (
      <div className={classList}>
        <div className="nameList">
          <div className="btnToggle" onClick={() => this.toggleList()}>
            <i className="fas fa-caret-down" />
            <i className="fas fa-caret-right" />
          </div>

          <span>{list.title}</span>

          <div
            className="btnCheckbox"
            onClick={() => handleCheckAllItem(list.id)}
          >
            <i className={classCheckAll} />
          </div>
        </div>

        <div className="boxItems">{listItems}</div>
      </div>
    );
  }
}
