import React, { Component } from "react";

import ItemList from "./ItemList";

import "../App.css";

const devices = [
  {
    id: "mobile",
    name: "Moblie Only"
  },
  {
    id: "desktop",
    name: "Desktop Only"
  }
];

const platforms = [
  {
    id: "facebook",
    title: "Facebook",
    itemList: [
      {
        id: "feed",
        name: "Feeds",
        default: true
      },
      {
        id: "instant_article",
        name: "Instant Articles",
        device: "mobile",
        default: true
      },
      {
        id: "right_hand_column",
        name: "Right column",
        default: true
      },
      {
        id: "marketplace",
        name: "Marketplace",
        device: "mobile",
        default: true
      },
      {
        id: "story",
        name: "Stories",
        default: true
      }
    ]
  },
  {
    id: "instagram",
    title: "Instagram",
    itemList: [
      {
        id: "stream",
        name: "Stream"
      },
      {
        id: "story",
        name: "Stories",
        default: true
      }
    ]
  },
  {
    id: "audience_network",
    title: "Audience Network",
    itemList: [
      {
        id: "classic",
        name: "Classic",
        default: true
      },
      {
        id: "instream_video",
        name: "In-stream Video",
        default: true
      },
      {
        id: "rewarded_video",
        name: "Rewarded Video",
        default: true
      }
    ]
  },
  {
    id: "messenger",
    title: "Messenger",
    itemList: [
      {
        id: "messenger_home",
        name: "Messenger Home",
        default: true
      },
      {
        id: "sponsored_messages",
        name: "Sponsored Mssages",
        default: false
      },
      {
        id: "story",
        name: "Stories",
        device: "mobile",
        default: true
      }
    ]
  }
];

export default class Assignments extends Component {
  state = {
    device: "",
    platforms: [],
    alert: false,
    contentAlert: ""
  };

  componentDidMount() {
    let dataPlatform = [];

    platforms.map(items => {
      const platform = items;
      let defaultItem = false;

      platform.itemList.map(item => {
        item.lock = false;
        item.selected = item.default;

        defaultItem = item.default ? true : defaultItem;

        return item;
      });

      platform.lock = false;
      platform.selected = defaultItem;

      return dataPlatform.push(platform);
    });

    this.setState({ platforms: dataPlatform });
  }

  componentWillUnmount() {}

  handleCheckAllItem = id => {
    const { platforms } = this.state;

    platforms.map(items => {
      if (items.id === id) {
        items.itemList.map(item => {
          return (item.selected = items.selected ? false : true);
        });

        items.selected = !items.selected;
      }

      return items;
    });

    this.setState({ platforms });
  };

  handleCheckItem = (idPlf, id) => {
    const { platforms } = this.state;
    let allCheck = false;
    let arrSelected = [];
    platforms.map(items => {
      allCheck = false;

      items.itemList.map(item => {
        if (items.id === idPlf && item.id === id)
          item.selected = !item.selected;

        allCheck = item.selected ? true : allCheck;

        arrSelected[items.id + item.id] = item.selected;
        return arrSelected;
      });

      items.selected = allCheck;

      return items;
    });

    this.checkRules(arrSelected);
    this.setState({ platforms });
  };

  // Validations
  checkRules = arrSelected => {
    // Check select Facebook Instant Article but not select Facebook Feed
    if (arrSelected.facebookinstant_article && !arrSelected.facebookfeed)
      return this.setState({
        alert: true,
        contentAlert:
          "To use Facebook Instant-Articles, please select the Facebook Feeds placement."
      });

    // Check select Facebook Marketplace but not select Facebook Feed
    if (arrSelected.facebookmarketplace && !arrSelected.facebookfeed)
      return this.setState({
        alert: true,
        contentAlert:
          "To use Facebook Marketplace, please select the Facebook Feeds placement."
      });

    // Check select both Instagram Stream, Instagram Stories
    if (arrSelected.instagramstory && arrSelected.instagramstream)
      return this.setState({
        alert: true,
        contentAlert:
          "Cannot select both options (Instagram Stream, Instagram Stories) for the same ad set."
      });

    // Check select Messenger Messenger Home but not select Facebook Feed
    if (arrSelected.messengermessenger_home && !arrSelected.facebookfeed)
      return this.setState({
        alert: true,
        contentAlert:
          "To use Messenger Home, please select the Facebook Feeds placement."
      });

    // Check select Messenger Stories but not select Facebook Feed
    if (
      arrSelected.messengerstory &&
      !arrSelected.facebookfeed &&
      !arrSelected.instagramstory
    )
      return this.setState({
        alert: true,
        contentAlert:
          "To use Messenger Story, please select the Facebook Feeds or Instagram Story placement."
      });

    return this.setState({ alert: false, contentAlert: "" });
  };

  changeValueSelect = event => {
    const device = event.target.value;
    const { platforms } = this.state;

    platforms.map(items => {
      items.itemList.map(item => {
        item.lock =
          device === "desktop" && item.device === "mobile" ? true : false;

        return item;
      });
      return items;
    });

    this.setState({ device, platforms });
  };

  render() {
    const listDevice = devices.map((item, key) => (
      <option value={item.id} key={key}>
        {item.name}
      </option>
    ));

    const { alert, contentAlert, platforms } = this.state;

    const listPlatforms = platforms.map((list, key) => (
      <ItemList
        key={key}
        list={list}
        handleCheckAllItem={id => this.handleCheckAllItem(id)}
        handleCheckItem={(idPlf, id) => this.handleCheckItem(idPlf, id)}
      />
    ));

    return (
      <div className="App">
        <div className="box">
          <div className="title">Device types</div>
          <select
            value={this.state.device}
            onChange={event => this.changeValueSelect(event)}
          >
            <option value="all">All devices (recommended)</option>
            {listDevice}
          </select>
        </div>

        <div className="box">
          <div className="title">Platforms</div>
          {alert && (
            <div className="alert">
              <span>
                <i className="fas fa-exclamation-circle" />
              </span>
              <p>{contentAlert}</p>
            </div>
          )}
          <div className="content">{listPlatforms}</div>
        </div>
      </div>
    );
  }
}
